.header{
  width: 80%;
  height: 100vh;
  padding: 1em 10%;
  position: relative;
  width: 100%;
  height: 440px;
  background: rgb(50, 50, 50);
  background: url(../../img/-superJumbo.jpg) no-repeat 100% 100% fixed;

  background-color:rgba(0,0,0,.5);
  overflow: hidden;
          @include mobile{
            height: 300px;
           background-size: auto;
           // width: auto;
          }
&__hero{
  position: absolute;
  top: 40%;
  left: 0;
  width: 100%;
  padding: .3em;
  font-size: 3em;
  font-weight: lighter;
}

  h1 {
    margin: 0;
    color: rgb(255, 251, 251);

    @include mobile{
      font-size: 38px;
     // width: auto;
    }
  }
}
