
  .nav{
        position: fixed;
        z-index: 9999;
        top: 0;
        left: 0;
        width: 100%;
        height: 35px;

  &__list{
    float: right;
    margin: 0;
    padding: .2em 2em;
  }
  &__item{
    display: inline-block;
    margin: 0;
    padding: .2em .7em;
  }
  &__link{
    display: flex;
    align-items: center;
    color: #fff;
    text-shadow: -2px 0px 16px rgba(255, 168, 55, 1),3px -1px 5px rgba(255, 247, 192, 0.97);
    img{
      margin-right: 15px;
    }

  }

  &:hover{
    color: rgb(220, 120, 0);
  }
  }

  .nav-bg {
    content: '';
    position: absolute;
    display: block;
    top: -100%;
    width: 100%;
    height: 100%;
    z-index: -1;
    background: rgb(50, 50, 50);
    transition: .45s ease-in-out;
  }
  .bg-hidden {
    top: -100%;
    opacity: 0;
  }

  .bg-visible {
    top: 0;
    opacity: 1;
  }
